.nav-menu {
  /* display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px; */
  display: flex;
  flex-direction: row;
  /* width: 100%; */
  justify-content: space-between;
  list-style: none;
  text-align: center;
  width: 60vw;
  /* justify-content: end; */
  margin-right: 2rem;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: #fff;
