    flex-basis: 40%;
    height: auto;
  }
  .event-card .description {
    flex-basis: 60%;
  }

  .event-card.alt {
    flex-direction: row-reverse;
  }
  .event-card.alt .description:before {
    left: inherit;
    right: -10px;
    transform: skew(3deg);
  }
  .event-card.alt .details {
    padding-left: 25px;
  }
  .heading {
    text-align: center;
    overflow: hidden;
  
    margin: 2rem, auto;
  
    position: relative;
    font-size: 1.5rem;
    letter-spacing: 0.1rem;
    color: #000;
  }
}
@media (max-width: 640px) {
  
  .heading {
    text-align: center;
    overflow: hidden;
  
    margin: 2rem, auto;
  
    position: relative;
    font-size: 1.5rem;
    letter-spacing: 0.1rem;
    color: #000;
  }
}

